.list-reset {
	list-style: none;
	margin: 0;
	padding: 0;
}

/*
BUTTONS
----------------------------------------------------------------*/

.btn {
	appearance: none;
	border: 0;
	background: none;
	text-decoration: none;
	color: var(--link-color);
	text-align: center;
	text-transform: lowercase;
	border-radius: 6px;
	font-weight: 500;
	box-shadow: currentColor 0 0 0 1px;
	padding: 0.3em 0.6em;
	white-space: nowrap;
	text-overflow: ellipsis;
	display: inline-block;
	overflow: hidden;
	max-width: 100%;

	&:not([disabled]) {
		cursor: pointer;
	}

	&:hover {
		text-decoration: none;
	}

	&:hover {
		text-decoration: none;
		color: var(--link-color-hover);
	}

	&:active,
	&:active {
		color: var(--link-color-active);
	}

	&[disabled] {
		color: var(--btn-color-disabled);
	}
}

/*
PROSE
----------------------------------------------------------------*/

.prose {
	&> :last-child {
		margin-bottom: 0;
	}

	& img,
	& iframe,
	& figure,
	& svg {
		--side-margin: calc(var(--lineheight) * 2);
		margin: var(--side-margin) auto;
		display: block;

		@media screen and (max-width: 480px) {
			--side-margin: var(--lineheight);
		}
	}

	@media screen and (prefers-color-scheme: dark) {
		& img {
			opacity: 0.9;
			box-shadow: rgba(0, 0, 0, 0.3) 0 0 6px;
		}
	}

	& ul,
	& ol {
		padding-left: 1em;
		margin-left: 1rem;

		& li>ul,
		& li>ol {
			padding-left: 1.5rem;
			margin-bottom: 0;
		}
	}

	& figcaption {
		padding: 1rem 0;
		text-align: center;
		font-family: var(--sans);
		font-size: 0.9em;
	}

	/* Break links and code */
	& code:not(pre > code),
	& a {
		word-break: break-word;
		word-wrap: break-word;
		overflow-wrap: break-word;
	}
}

& figure.image--full {
	margin: calc(var(--lineheight) * 2) -14vw;

	& img {
		display: block;
		margin: 0 auto;
	}

	@media screen and (max-width: 960px) {
		/* Edge-to-edge pixelfucking. */
		margin-left: auto;
		margin-right: auto;
	}
}

/*
OPEN TYPE
----------------------------------------------------------------*/

.plain-font-features {
	font-feature-settings:
		/* Contextual alternates */
		'calt' 1, 'kern' 1,
		'liga' 1,
		/* Open digis */
		'ss01' 1,
		/* Curved 'r' */
		'ss03' 1,
		/* Disambiguation */
		'ss04' 0, 'frac' 1;
}

