/*
TYPOGRAPHY
----------------------------------------------------------------*/

.font-mono {
	font-family: var(--mono);
}

/* Measure is limited to ~78 characters */
.measure {
	max-width: var(--measure);
}

/* Measure is limited to ~80 characters */
.measure-wide {
	max-width: var(--measure-wide);
}

/* Measure is limited to ~45 characters */
.measure-narrow {
	max-width: var(--measure-narrow);
}

.uppercase {
	text-transform: uppercase;
}

.tracking {
	letter-spacing: 1px;
}

.rounded-small {
	border-radius: .25em;
}

.no-underline {
	text-decoration: none;

	&:hover {
		text-decoration: underline;
	}
}

.badge {
	font-size: var(--f7);
	text-transform: lowercase;
	border-radius: 4px;
	font-weight: 500;
	background-color: var(--neon);
	color: var(--color-bg);
	padding: 0.4em 0.8em;

	&:hover {
		text-decoration: none;
	}
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border-width: 0;
}

.sep {
	color: var(--color-text-detail);
	display: inline-block;
	margin: 0 0.5em;
}

.no-wrap {
	white-space: nowrap;
}

.checkbox-fix {
	position: relative;
	top: -1px;
}

.truncate {
	white-space: nowrap;
	text-overflow: ellipsis;
	display: inline-block;
	overflow: hidden;
	max-width: 100%;
}

.icon {
	fill: currentColor;
	width: 1em;
	height: auto;
	aspect-ratio: 1 / 1;
	vertical-align: middle;
	display: inline-block;
	font-size: .9em;
}

a:hover .icon-feed {
	color: var(--yellow);
}
