.NavCommon {
	& a {
		color: var(--color-text);
		white-space: nowrap;
		text-decoration: none;

		&:hover {
			color: var(--link-color-hover);
			text-decoration: underline;
		}
	}
}

.MainNav {
	margin-right: 3rem;
	width: var(--sidenav-width);
	position: relative;
	z-index: 2;

	& h1>a {
		font-weight: 500;
		color: var(--color-text);
		white-space: nowrap;
		text-decoration: none;
	}

	& a {
		&:is(:hover, :focus-visible) {
			outline: 0;
			color: var(--link-color-hover);
			text-decoration: underline;
		}
	}

	& ul {
		top: var(--lineheight);
		position: sticky;
		/* Align with back btn arrow */
		margin-left: 2.2rem;

		& a {
			font-weight: normal;
			position: relative;

			&:not([aria-current='page'] > &):not(&:hover):not(&:focus-visible) {
				opacity: .8;
			}

			:where([aria-current='page'] &, &:focus) {
				outline: 0;

				&::before {
					content: '⇢';
					display: block;
					position: absolute;
					right: calc(100% + 0.5em);
					white-space: nowrap;
				}
			}
		}

		& [aria-current='page'] a {
			font-weight: 700;
		}
	}

	& h1 {
		--offset-top: 18px;
		line-height: calc(var(--lineheight) * 3);
		margin-bottom: -4px;
		padding-top: var(--offset-top);
	}

	@media screen and (max-width: 980px) {
		width: calc(100% + var(--inset) * 2);
		margin: calc(var(--inset) * -1);
		padding: calc(var(--inset) * 1);
		margin-bottom: calc(var(--lineheight) / 2 - 1px);
		position: sticky;
		inset: 0;
		overflow-x: auto;
		background-color: var(--color-bg);
		font-size: var(--f5);

		& h1 {
			position: absolute;
			left: -9999em;
		}

		& ul {
			display: flex;
			margin: 0;
			flex-direction: row;
			gap: .75em;
		}

		& [aria-current='page'] a,
		& a:focus {
			&::before {
				display: none;
			}
		}
	}
}

.BackLink {
	text-decoration: none;
	color: var(--color-text);
	background-color: var(--grey-lightest);
	width: 1.6em;
	text-align: center;
	line-height: 1.6em;
	aspect-ratio: 1/1;
	display: inline-block;
	border-radius: 4em;
	opacity: .8;

	a:is(:hover, :focus) > & {
		opacity: 1;
	}
}

.home [role="navigation"] {
	@media screen and (min-width: 530px) {
		& ul {
			display: flex;
		}

		& li {

			&:not(:last-child)::after {
				content: ' / ';
				display: inline-block;
				margin: 0 .75em;
				color: var(--color-text-detail);
				transform: scaleY(1.4);
			}
		}
	}

	@media screen and (max-width: 529px) {
		columns: 2;
		text-align: center;
		line-height: 2;
	}

	@media screen and (max-width: 240px) {
		columns: 1;
	}
}
